<template>
  <div>
    <md-dialog :md-active.sync="showDialog" style="width:50%;padding:20px;">
      <md-dialog-title>New User</md-dialog-title>
      <md-field>
        <label>FirstName</label>
        <md-input v-model="newUser.firstName" required></md-input>
      </md-field>

      <md-field>
        <label>LastName</label>
        <md-input v-model="newUser.lastName" required></md-input>
      </md-field>

      <md-field>
        <label>Email</label>
        <md-input v-model="newUser.email" required></md-input>
      </md-field>

      <md-field>
        <label>Country</label>
        <md-input v-model="newUser.country" required></md-input>
      </md-field>
      <md-field>
        <label for="gender">Gender</label>
        <md-select v-model="newUser.gender" name="gender" id="gender">
          <md-option value="Male">Male</md-option>
          <md-option value="Female">Female</md-option>
        </md-select>
      </md-field>

      <md-dialog-actions>
        <md-button class="md-primary" @click="showDialog = false">Close</md-button>
        <md-button v-if="!isDataUpdate && !isDeleteuser" class="md-primary" @click="addNewUser">Add</md-button>
        <md-button v-if="isDataUpdate" class="md-primary" @click="updateExitingUser">Update</md-button>
        <md-button v-if="isDeleteuser" class="md-primary" @click="updateExitingUser">Delete</md-button>
      </md-dialog-actions>
    </md-dialog>

    <md-app-toolbar md-elevation="0">
      <md-field style="width:30%;min-width:50px">
        <label>search</label>
        <md-input v-model="search"></md-input>
      </md-field>

      <!-- <span @click="showAddUserPopup">Add User</span> -->
      <md-button class="md-raised md-primary" @click="showAddUserPopup">Add User</md-button>
    </md-app-toolbar>

    <md-card v-for="(uData,index) in userDataFilter" v-bind:key="index">
      <md-card-header>
        <md-card-header-text>
          <div class="md-title">{{uData.firstName}}</div>
          <div class="md-subhead">Medium size</div>
        </md-card-header-text>

        <md-card-media md-medium>
          <img
            src="https://cdn1.iconfinder.com/data/icons/business-users/512/circle-512.png"
            alt="People"
          />
        </md-card-media>
      </md-card-header>

      <md-card-actions>
        <md-button @click="assignExitingUser(uData,index,'update')">Update</md-button>
        <md-button @click="assignExitingUser(uData,index,'delete')">Delete</md-button>
      </md-card-actions>
    </md-card>
  </div>
</template>



<style scoped>
.md-card {
  width: 320px;
  margin: 4px;
  display: inline-block;
  vertical-align: top;
}
.md-button.md-theme-default.md-raised:not([disabled]).md-primary {
  position: absolute;
  right: 10px;
}
</style>
<script>
import userData from "../assets/userData.json";
export default {
  name: "users",
  data() {
    return {
      userData: userData,
      showDialog: false,
      search: "",
      newUser: {},
      isDataUpdate: false,
      isDeleteuser: false
    };
  },
  methods: {
    showAddUserPopup() {
      this.showDialog = true;
      this.isDataUpdate = false;
      this.isDeleteuser = false;
      this.newUser = {};
    },
    addNewUser() {
      this.showDialog = false;
      this.userData.push(this.newUser);
    },
    assignExitingUser(userData, index, status) {
      if (status == "delete") {
        this.isDeleteuser = true;
        this.isDataUpdate = false;
      } else {
        this.isDataUpdate = true;
        this.isDeleteuser = false;
      }
      this.showDialog = true;
      this.selectedUserIndex = index;

      this.newUser = userData;
    },
    updateExitingUser() {
      this.showDialog = false;
      this.isDataUpdate = false;
      this.userData[this.selectedUserIndex] = this.newUser;
    }
  },
  computed: {
    userDataFilter() {
      return this.userData.filter(post => {
        return post.firstName.toLowerCase().includes(this.search.toLowerCase());
      });
    }
  }
};
</script>